                    add_info: [require('@/assets/resetPic/t2-1.png'), require('@/assets/resetPic/t2-2.png'), require('@/assets/resetPic/t2-3.png'), require('@/assets/resetPic/t2-4.png'), require('@/assets/resetPic/t2-5.png')]
                },
