                    title: '员工申请流程',
                    add_info: [require('@/assets/resetPic/t5-1.png'), require('@/assets/resetPic/t5-2.png'), require('@/assets/resetPic/t5-3.png'), require('@/assets/resetPic/t5-4.png'), require('@/assets/resetPic/t5-5.png')]
                },
                {
