                    add_info: [require('@/assets/resetPic/t6-1.png'), require('@/assets/resetPic/t6-2.png'), require('@/assets/resetPic/t6-3.png'), require('@/assets/resetPic/t6-4.png')]
                },
                {
                    title: 'ota订单手动同步操作流程',
                    add_info: [require('@/assets/resetPic/t7-1.png'), require('@/assets/resetPic/t7-2.png'), require('@/assets/resetPic/t7-3.png')]
                },
                {
                    title: 'ota订单自动同步操作流程',
                    add_info: [require('@/assets/resetPic/t8-1.png'), require('@/assets/resetPic/t8-2.png'), require('@/assets/resetPic/t8-3.png'), require('@/assets/resetPic/t8-4.png')]
                },
                {
                    title: '房间号更换房型操作',
                    add_info: [require('@/assets/resetPic/t9-1.png'), require('@/assets/resetPic/t9-2.png'), require('@/assets/resetPic/t9-3.png'), require('@/assets/resetPic/t9-4.png')]
                },
                {
                    title: '长租房办理操作流程',
                    add_info: [require('@/assets/resetPic/t10-1.png'), require('@/assets/resetPic/t10-2.png'), require('@/assets/resetPic/t10-3.png'), require('@/assets/resetPic/t10-4.png'), require('@/assets/resetPic/t10-5.png')]
                },
                {
                    title: '挂牌价，日历价格调整操作',
