                },
                {
                    title: '财务报表查看流程',
                    add_info: [require('@/assets/resetPic/t12-1.png'), require('@/assets/resetPic/t12-2.png'), require('@/assets/resetPic/t12-3.png'), require('@/assets/resetPic/t12-4.png'), require('@/assets/resetPic/t12-5.png')]
                },
                {
                    title: '订单退款操作流程',
                    add_info: [require('@/assets/resetPic/t13-1.png'), require('@/assets/resetPic/t13-2.png'), require('@/assets/resetPic/t13-3.png'), require('@/assets/resetPic/t13-4.png'), require('@/assets/resetPic/t13-5.png')]
                },
                {
                    title: '离线密码获取方法(酒店停电断网应急)',
                    add_info: [require('@/assets/resetPic/t14-1.png'), require('@/assets/resetPic/t14-2.png'), require('@/assets/resetPic/t14-3.png'), require('@/assets/resetPic/t14-4.png'), require('@/assets/resetPic/t14-5.png')]
                },
                {
                    title: '扫码入住操作流程',
                    add_info: [require('@/assets/resetPic/t15-1.png'), require('@/assets/resetPic/t15-2.png'), require('@/assets/resetPic/t15-3.png'), require('@/assets/resetPic/t15-4.png'), require('@/assets/resetPic/t15-5.png')]
                },
