                },
                {
                    title: '连住折扣开通流程',
                    add_info: [require('@/assets/resetPic/t17-1.png'), require('@/assets/resetPic/t17-2.png'), require('@/assets/resetPic/t17-3.png'), require('@/assets/resetPic/t17-4.png'), require('@/assets/resetPic/t17-5.png')]
                },
                {
                    title: '门店会员等级设定流程',
                    add_info: [require('@/assets/resetPic/t18-1.png'), require('@/assets/resetPic/t18-2.png'), require('@/assets/resetPic/t18-3.png'), require('@/assets/resetPic/t18-4.png'), require('@/assets/resetPic/t18-5.png')]
                },
                {
                    title: '如何添加消费商品指南',
                    add_info: [require('@/assets/resetPic/t19-1.png'), require('@/assets/resetPic/t19-2.png'), require('@/assets/resetPic/t19-3.png'), require('@/assets/resetPic/t19-4.png'), require('@/assets/resetPic/t19-5.png')]
                }
            ]
        };
    },
    created() {
        this.$store.commit('setHeader', '功能更新');
    },
    methods: {
        goNext(item, index) {
            this.$router.push({
                path: '/resetinfo',
                query: {
                    picArr: item.add_info,
                    title: item.title,
                    index
                }
            });
        }
    }
};
</script>
<style scoped>
#scroll_reSet {
    position: fixed;
    top: 40px;
    bottom: 10px;
    left: 0;
    right: 0;
    background: #fff;
    overflow-y: scroll;
}
.top_title {
    text-align: left;
    font-size: 14px;
    font-weight: bold;
    padding: 10px;
}
#box_reset {
    display: flex;
    justify-content: space-between;
    font-size: 14px;
    align-items: center;
    padding: 10px 0;
    border-bottom: 1px solid #efeff4;
    margin: 0 12px;
}
#box_reset > .box-left {
    text-align: left;
    line-height: 20px;
}
#box_reset > .box-left > :nth-child(2) {
    color: #cecece;
}
#box_reset > .box-right {
    color: #b2b2b2;
    opacity: 0.3;
    width: 20px;
    height: 20px;
}
</style>


